@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&family=Playfair+Display:ital,wght@0,400;1,400;1,500;1,600&display=swap");

/* Page Template */
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	/* font-family: "Jost", sans-serif; */
	list-style: none;
	text-decoration: none;
	scroll-behavior: smooth;
}

:root {
	--champagne-pink: #eed7c5ff;
	--tuscany: #c89f9cff;
	--copper-red: #c97c5dff;
	--copper-penny: #b36a5eff;
	--antique-brass: #cb997eff;
	--desert-sand: #ddbea9ff;
	--champagne-pink: #ffe8d6ff;
	--ash-gray: #b7b7a4ff;
	--artichoke: #a5a58dff;
	--ebony: #6b705cff;
	--light-gray: #c1bebe;
}

body {
	background: var(--ebony);
	color: var(--light-gray);
}

/* Default settings */

.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
/* Nav Bar */
nav {
	float: right;
	margin-left: 2%;
	font-size: 22px;
	font-family: "Jost", sans-serif;
}

nav ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

nav li {
	display: inline-block;
	margin-right: 85px;
	padding-top: 25px;

	position: relative;
}

nav a {
	color: var(--light-gray);
	text-decoration: none;
	text-transform: uppercase;
	font-size: 20px;
}

nav a:hover {
	color: #000;
}

nav a::before {
	content: "";
	display: block;
	height: 5px;
	width: 100%;
	background-color: rgb(171, 117, 117);

	position: absolute;
	top: 0;
	width: 0%;

	transition: all ease-in-out 250ms;
}

nav a:hover::before {
	width: 100%;
}

/* Name Styling */

.container {
	margin-top: 1%;
	animation-name: change-color;
	animation-duration: 5s;
}

.text-box {
	position: absolute;
	top: 22%;
	margin-left: 10%;
	-webkit-transition: all 1s ease-in-out;
	-moz-transition: all 1s ease-in-out;
	-o-transition: all 1s ease-in-out;
	-ms-transition: all 1s ease-in-out;
	font-family: "Jost", sans-serif;
}

.text-box p {
	font-size: 50px;
	font-weight: 600;
}

.text-box h1 {
	font-size: 190px;
	line-height: 160px;
	margin-left: -10px;
	color: transparent;
	-webkit-text-stroke: 1px #fff;
	background: url(../public/assets/images/back.png);
	-webkit-background-clip: text;
	background-position: 0 0;
	animation: back 20s linear infinite;
}
@keyframes back {
	100% {
		background-position: 2000px 0;
	}
}

.text-box h3 {
	font-size: 40px;
	font-weight: 500;
}

.text-box .row :hover {
	color: #000;
	cursor: pointer;
}

.text-box .row {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	margin-top: 30px;
}

.text-box a {
	color: #fff;
	text-decoration: none;
	padding: 10px 20px;
	margin-right: 20px;
	border: 2px solid #fff;
}

.text-box a span {
	font-size: 30px;
	line-height: 15px;
	margin-left: 5px;
}

.me {
	margin-left: 65%;
	margin-top: 5%;
	border-radius: 25px;
	border: 2px solid var(--light-gray);
	padding: 20px;
}

